import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CanalService {

  private canalValue: string;
  private userId: number;
  private userIdFoCommand: number;
  private commandToPrepareLength: number;

  getCommandToPrepareLength(): number {
    return this.commandToPrepareLength;
  }

  setCommandToPrepareLength(value: number) {
    this.commandToPrepareLength = value;
  }

  getUserIdFoCommand(): number {
    return this.userIdFoCommand;
  }

  setUserIdFoCommand(value: number) {
    this.userIdFoCommand = value;
  }

  setCanal(value: string) {
    this.canalValue = value;
  }

  getCanal(): string {
    return this.canalValue;
  }

  getUserId(): number {
    return this.userId;
  }

  setUserId(value: number) {
    this.userId = value;
  }
}
