import {Injectable} from '@angular/core';
// @ts-ignore
import {Food} from '../models/food.model';
import {HttpClient} from '@angular/common/http';
import {Observable,} from 'rxjs';
import {concatMap, filter} from 'rxjs/operators';

//const baseUrl = 'http://192.168.0.246:8080/api/client/foods';
const baseUrl = 'https://oafish-orange-production.up.railway.app/api/client/foods';

//const urlBublished = 'http://192.168.0.246:8080/api/client/foods/published';
const urlBublished = 'https://oafish-orange-production.up.railway.app/api/client/foods/published';


@Injectable({
  providedIn: 'root',
})
export class FoodClientService {
  constructor(private http: HttpClient) {
  }

  getAll(): Observable<Food[]> {
    return this.http.get<Food[]>(baseUrl);
  }

  getAllFoodUserPublished(idUser: number): Observable<Food[]> {
    return this.http.get<Food[]>(`${baseUrl}/user/${idUser}`);
  }

  getAllPublished(): Observable<Food[]> {
    return this.http.get<Food[]>(urlBublished);
  }

  getFoodsById(id: number): Observable<Food> {
    // @ts-ignore
    return this.http.get(`${baseUrl}/${id}`);
  }

  delete(id: any): Observable<any> {
    return this.http.delete(`${baseUrl}/${id}`);
  }
  create(data: any): Observable<any> {
    return this.http.post(baseUrl, data);
  }
  update(id: any, data: any): Observable<any> {
    return this.http.put(`${baseUrl}/${id}`, data);
  }

  findByTitle(idUser: number, title: any): Observable<Food[]> {
    return this.http.get<Food[]>(`${baseUrl}/user/${idUser}/?title=${title}`);
  }
}
