import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {BehaviorSubject, interval, Observable, Subject} from 'rxjs';
import {tap, switchMap, startWith, scan} from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class CommandsToPrepareService {
  //private apiUrl = 'http://192.168.0.246:8080/api/command/commandsToPrepare';
  private apiUrl = 'https://oafish-orange-production.up.railway.app/api/command/commandsToPrepare/';
  private refreshInterval = 5000; // Temps d'actualisation en millisecondes (ici, toutes les 5 secondes)
  // eslint-disable-next-line @typescript-eslint/member-ordering
  public notificationSound = new Audio('assets/audio/songOrder.mp3');
  private previousData: any[] = [];


  private userIdSubject = new BehaviorSubject<number>(0); // initialiser avec une valeur par défaut de type number
  private allCommandReceivedSubject = new Subject<any[]>();
  private allCommandReceived$: Observable<any[]>;

  constructor(private http: HttpClient) {
    this.allCommandReceived$ = this.userIdSubject.pipe(
      switchMap(userId => this.allCommandsByUserId(userId)),
    );
  }
  allCommandsByUserId(userId: number): Observable<any[]> {
    const url = `${this.apiUrl}/getByUser/${userId}`;
    return interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<any[]>(url)),
      tap((data) => {
        this.allCommandReceivedSubject.next(data);
        this.previousData = data; // Mise à jour des données précédentes
      })
    );
  }

  allCommandsInversedByUserId(userId: number): Observable<any[]> {
    const url = `${this.apiUrl}/ordres-inverse/${userId}`;
    return interval(this.refreshInterval).pipe(
      startWith(0),
      switchMap(() => this.http.get<any[]>(url)),
      tap((data) => {
        this.allCommandReceivedSubject.next(data);
        this.previousData = data; // Mise à jour des données précédentes
      })
    );
  }

  deleteCommand(id: number): Observable<void> {
    const url = `${this.apiUrl}/deleteCommand/${id}`;
    return this.http.delete<void>(url).pipe(
      // Mettez à jour la liste des commandes locales en filtrant la commande supprimée
      tap(() => {
        this.previousData = this.previousData.filter((command) => command.id !== id);
      })
    );
  }
  /*deleteCommande(commandeId: number): Observable<any> {
    const deleteUrl = `${this.apiUrl}${commandeId}`;
    return this.http.delete<any>(deleteUrl);
  }*/

  // eslint-disable-next-line @typescript-eslint/member-ordering
  create(data: any): Observable<any> {
    return this.http.post(`${this.apiUrl}`, data);
  }


}
